import React from 'react'
import { connect } from 'react-redux'
import { Layout, Row, Col, Table, Select } from 'antd'
import { fetchAll } from '../../models/device'

function stringToHex (input) {
  let hex
  let output = ''
  for (var i = 0; i < input.length; i++) {
    let charCode = input.charCodeAt(i)

    hex = (charCode & 0xff).toString(16)
    hex = (hex.length === 1) ? '0' + hex : hex
    output += ` ${hex}`
  }

  return output
}

export class Gateway extends React.Component {
  state ={
    activeKey: ""
  };

  componentDidMount () {
    const { fetchAll } = this.props
    fetchAll()
  }

  handleChange = (activeKey) => this.setState({ activeKey })

  render () {
    let activeKey = ''
    const { devices, nodes } = this.props
    const data = devices.filter(device => device.board[0] === '1')
    if (data.length > 0) activeKey = this.state.activeKey || data[0].deviceId

    const nodeList = nodes.filter(node => activeKey === '' || node.gwId === activeKey)
      .map(node => {
        const device = devices.find(dev => dev.deviceId === node.devId)
        return device ? { ...device, realtime: stringToHex(atob(node.data)) } : {
          deviceId: node.devId,
          name: node.devId,
          realtime: stringToHex(atob(node.data)),
          swVer: '',
          hwVer: '',
          libVer: '',
          last_join: 0
        }
      })

    return (<Layout>
      <div style={{ padding: '0 18px 24px' }}>
        <Select style={{ width: 300 }} onChange={this.handleChange} value={activeKey}>
          {data.map((gateway, index) => (
            <Select.Option key={index} value={gateway.deviceId}>
              {gateway.name}
            </Select.Option>
          ))}
        </Select>
      </div>
      <Row gutter={36} style={{ padding: '0 18px' }}>
        <Col span={24} >
          <h3 style={{ paddingLeft: 8 }}>节点列表</h3>
          <Table dataSource={nodeList} rowKey='deviceId'>
            <Table.Column title='节点名称' dataIndex='name' key='name' width={120} />
            <Table.Column title='固件版本' dataIndex='swVer' key='swVer' width={100} />
            <Table.Column title='硬件版本' dataIndex='hwVer' key='hwVer' width={100} />
            <Table.Column title='库版本' dataIndex='libVer' key='libVer' width={80} />
            <Table.Column title='上线时间' dataIndex='last_join' key='last_join' width={120}
              render={text => {
                const date = new Date(parseInt(text, 10))
                return `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDay()}`
              }} />
            <Table.Column title='实时数据' dataIndex='realtime' key='realtime' />
          </Table>
        </Col>
      </Row>
    </Layout>)
  }
}

const mapDispatchToProps = {
  fetchAll
}

const mapStateToProps = state => ({
  devices: state.device.list,
  nodes: state.websocket.collection
})

export default connect(mapStateToProps, mapDispatchToProps)(Gateway)



